// ------------------ Fonts
@import "~@fontsource/montserrat/latin-300.css";
@import "~@fontsource/montserrat/latin-400.css";
@import "~@fontsource/montserrat/latin-600.css";

// ------------------ Modules
@import "nano-grid/scss/nano-grid";
@import "nano-grid/scss/btn";
@import "nano-grid/scss/scroll-area";
@import "nano-grid/scss/tooltip";

* {
  box-sizing: border-box;
}

html,
body {
  font-family: "Montserrat", Helvetica, Arial, Tahoma, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #222;
  overflow: hidden;
}

main {
  &.nano-light {
    --color-title: #9c330a;
    --color-background: #ffffff;
    --color-foreground: #1e1e1e;
    --color-workarea-background: #f8f7fb;
    --color-panel-details-background: #ffffff;
    --color-panel-background: #272b42;
    --color-panel-border-separator: #cccccc;
    --color-border: #e2e2e2;
    --color-palette: #f7f7f7;
    --color-label: #104e86;
    --color-legend: #9c330a;
    --color-background-input: #dadada;
    --color-table-background: hsl(240deg 20% 80% / 0.2);
    --color-table-background-2: hsl(240deg 20% 85% / 0.2);
    --color-table-background-hover: hsl(240deg 35% 90% / 0.2);
  }
  &.nano-dark {
    --color-title: #e69b28;
    --color-background: #222;
    --color-foreground: #eee;
    --color-workarea-background: #1e1e1e;
    --color-panel-details-background: #1f1f23;
    --color-panel-background: #232327;
    --color-panel-border-separator: #19191d;
    --color-border: #444444;
    --color-palette: #272727;
    --color-label: #4ca1ef;
    --color-legend: #b9b90e;
    --color-background-input: #272731;
    --color-table-background: hsl(240deg 5% 25% / 0.2);
    --color-table-background-2: hsl(240deg 5% 30% / 0.2);
    --color-table-background-hover: hsl(240deg 20% 35% / 0.2);
  }
  --title-shadow: 0;
  --color-shadow: rgb(0 0 0 / 65%);
  --shadow-depth: 2px 2px 5px;
  color: var(--color-foreground);
}

